html[data-theme='dark'] {
    --main-font: 'Montserrat', sans-serif;
    --main-color: #B6452C;
    --sub-color: #301B28;
    --up-text-color: #fff;
    --text-color: #fff;
    --background-color: #021C1E;
}

html[data-theme='light'] {
    --main-font: 'Montserrat', sans-serif;
    --main-color: #66A5AD;
    --sub-color: #6C2D2C;
    --up-text-color: #fff;
    --text-color: #000;
    --background-color: #F1DCC9;
}

body {
    background-color: var(--background-color);
    transition: .2s linear;
}

ul {
    list-style-type: none;
    padding-left: 0;
}

li {
    margin-left: 10px;
    color: var(--text-color);
    margin-top: 10px;
    font-size: 17px;
}

h2, p {
    font-weight: 400;
    color: var(--text-color);
}

a {
    color: var(--text-color);
    text-decoration: none;
    cursor: pointer;
}

.justify-box {
    display: flex;
}

header {
    box-shadow: 0 15px 30px 10px rgba(0, 0, 0, .1);
    max-width: 100%;
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 2px solid var(--main-color);
    border-radius: 0 0 10px 10px;
    position: fixed;
    top: 0;
    left: 50%;
    transform: translate(-50%);
    transition: all .4s ease-out;
    z-index: 1;
    background-color: var(--sub-color);
}

.burger {
    display: none;
    border: none;
    height: 45px;
    border-radius: 5px;
    padding: 5px 0 0;
    align-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 5px;
    flex-direction: column;
    background: transparent;
    margin: 15px 0 0 25px;
}

.burger::after, .burger::before, .burger-inner {
    content: '';
    width: 35px;
    height: 3px;
    background-color: var(--up-text-color);
    border: none;
    border-radius: 5px;
    margin-bottom: 5px;
    transition: all .3s linear;
    transform-origin: left;
}

.burger::after {
    transform-origin: left bottom;
}

.burger-active::before {
    transform: rotate(27deg);
}

.burger-active .burger-inner {
    width: 0;
}

.burger-active::after {
    transform: rotate(-27deg);
    bottom: 10px;
    margin-bottom: 1px;
}

nav {
    padding-left: 30px;
}

nav span {
    color: var(--up-text-color);
    font-size: 20px;
    text-transform: uppercase;
    margin-right: 25px;
    transition: color linear .2s;
}

nav span:hover {
    color: var(--main-color);
}

header svg {
    margin-right: 60px;
    cursor: pointer;
}

header svg:hover * {
    fill: var(--main-color);
}

.light {
    display: none;
}

.aside-bar {
    position: fixed;
    padding-left: 30px;
    height: 100vh;
    max-width: 20%;
    width: 20%;
    opacity: .9;
    overflow-y: scroll;
    transition: all .5s ease;
}

.aside-bar h2 {
    margin-top: 70px;
    line-height: 1;
}

.accorLink:hover + .arrow {
    opacity: 1;
}

.arrow {
    width: 20px;
    height: 100%;
    opacity: 0;
    transition: .2s ease;
}

.arrow * {
    fill: var(--text-color);
}

.classUL, .themeUL {
    display: none;
}

main {
    display: flex;
    justify-content: right;
}

main p {
    padding-top: 70px;
    max-width: 75%;
    width: 75%;
}

@media screen and (max-width: 375px) {
    header svg {
        margin-right: 40px;
    }

    .aside-bar {
        width: 0;
        max-width: 0;
        padding: 0;
        background-color: var(--main-color);
    }

    .active-aside {
        max-width: 75%;
        width: 75%;
        padding-left: 30px;
    }

    .burger {
        display: flex;
    }

    nav {
        display: none;
    }

    main {
        justify-content: left; 
    }

    main p {
        margin-left: 3%;
    }
}